'use client';

import React, { useState, useMemo } from 'react';
import { doc } from 'firebase/firestore';
import { useUser, useFirestore } from '@/firebase/provider';
import { useDoc } from '@/firebase/firestore/use-doc';
import { OnboardingChecklist } from '@/components/onboarding-checklist';
import { EmailVerificationBanner } from '@/components/email-verification-banner';
import { type User as UserProfile } from '@/lib/types';

interface AppLayoutClientProps {
  children: React.ReactNode;
}

export function AppLayoutClient({ children }: AppLayoutClientProps) {
  const { user: authUser, isUserLoading: isAuthLoading } = useUser();
  const firestore = useFirestore();

  // Memoriza a referência do documento para evitar loops de renderização infinitos
  const userDocRef = useMemo(() => {
    if (authUser && firestore) {
      return doc(firestore, 'users', authUser.uid);
    }
    return null;
  }, [authUser, firestore]);

  const { data: userData, isLoading: isUserDataLoading } = useDoc<UserProfile>(userDocRef);

  const [showOnboarding, setShowOnboarding] = useState(true);

  const isLoading = isAuthLoading || (authUser && isUserDataLoading);

  if (isLoading) {
    return <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">Carregando...</div>;
  }

  if (!authUser) {
    return null; // Should be handled by middleware
  }

  const isPro = userData?.plan === 'pro';
  const onboardingCompleted = userData?.onboardingCompleted || (userData?.onboardingProgress?.length || 0) >= 4;
  const needsOnboarding = !isPro && !onboardingCompleted && showOnboarding;

  const handleCloseOnboarding = () => {
    setShowOnboarding(false);
  };

  return (
    <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
      <EmailVerificationBanner />
      {needsOnboarding && userDocRef && userData && (
        <OnboardingChecklist 
          userData={userData} 
          userDocRef={userDocRef}
          onClose={handleCloseOnboarding}
        />
      )}
      {children}
    </div>
  );
}
